
body {
  font-family: Arial, sans-serif;
}

.container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  /* color: #44475b; */
  border-radius: 5px;
  color: transparent;
  -webkit-background-clip: text;
  font-size: 33px;
  font-weight: bolder;
  background-color: aliceblue;
  height: 30px;
}
p {
  margin-top: 16px;
  color: #00b386;
  font-weight: 700;
  font-size: 1rem;
}

label {
  display: flex;
  margin: 10px 25px;
  color: #047761;
  align-items: center;
  justify-content: space-between;
}

input {
  width: 52px;
  padding: 0px 2px;
  /* margin: 0px 0px; */
  box-sizing: border-box;
  display: flex;
  text-align: right;
  border: none;
  outline: 0px;
  opacity: 1;
  font-weight: 700;
  background: #ebf9f5;
  caret-color: #00b8f5;
  align-items: center;
  height: 30px;
  border-radius: 4px;
  color: #00b386;
  font-size: 1rem;

}

.header-container {
  margin: 0px 0px;
  /* border-radius: 20px; */
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #00b386;
}
.getvalue{
  display: flex;
  align-items: center;
  background: #ebf9f5;
  height: 30px;
  border-radius: 4px;
}

.calculator-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
  background-color: #ffffff;
}

/* .calculator-container:hover {
  background-color: #e0e0e0;
  cursor: pointer;
} */

.result-container {
  margin: 32px 12px;
  border-radius: 20px;
  font-family: none;
  font-size: 15px;
  padding: 1px;
}

.result-Value {
  display: flex;
  background-color: #ebf9f5;
  /* margin: 10px 0px 0px 0px; */
  border-radius: 0px;
  align-items: baseline;
  justify-content: center;
}

strong {
  color: #898484;
  padding: 0px 5px 0px 0px;
  /* margin: 10px -5px 5px 15px; */
  font-family: none;
}

